export default {
  name: "add",
  layout: "admin",
  data() {
    return {
      title: '',
      desk: ""
    }
  },
  methods: {
    async addItem() {
      await this.$axios.$post('http://localhost:8080/catalog/create', {
        title: this.title,
        desc: this.desk
      })
    }
  }
}
